	module.exports = {"data":{"nodePost":{"title":"Ricky Jay's Magical Secrets","link":"https://www.newyorker.com/magazine/1993/04/05/secrets-of-the-magus","body":{"value":"<blockquote>People ask me why I don’t do lectures at magic conventions, and I say, ‘Because I’m still learning.’ Meanwhile, you’ve got people who have been doing magic for ten months and they are actually out there pontificating. It’s absurd.”</blockquote>\r\n<p>I've been catching up on a lot of stuff related to sleight of hand magician Ricky Jay after he passed away last week. The quote above from a fantastic 1993 New Yorker profile kept rattling around in my head. Given that his whole career is based on deception, you can't take anything he says at face value (pun intended,) but it is wonderful to think that one of the worlds best magicians still considered himself a student.</p>\r\n<p>As someone who has done a lot of speaking this year I often feel this way. \"Why should people listen to me - I just figured this out a couple of weeks ago.\" What allows me to fight back the imposter syndrome is the fact that I believe understanding how someone learned something has a lot of value. And I think this is the case for beginners and experts alike.</p>"},"relationships":{"image":null},"created":1543327994}},"pathContext":{"slug":"/link/ricky-jay's-magical-secrets/"}}

